module.exports = graphql;

var GraphqlError = require('./error');

var NON_VARIABLE_OPTIONS = ['method', 'baseUrl', 'url', 'headers', 'request', 'query'];

function graphql(request, query, options) {
  if (typeof query === 'string') {
    options = Object.assign({
      query: query
    }, options);
  } else {
    options = query;
  }

  var requestOptions = Object.keys(options).reduce(function (result, key) {
    if (NON_VARIABLE_OPTIONS.includes(key)) {
      result[key] = options[key];
      return result;
    }

    if (!result.variables) {
      result.variables = {};
    }

    result.variables[key] = options[key];
    return result;
  }, {});
  return request(requestOptions).then(function (response) {
    if (response.data.errors) {
      throw new GraphqlError(requestOptions, response);
    }

    return response.data.data;
  });
}