module.exports = Octokit;

var _require = require('@octokit/request'),
    request = _require.request;

var Hook = require('before-after-hook');

var parseClientOptions = require('./parse-client-options');

function Octokit(plugins, options) {
  options = options || {};
  var hook = new Hook.Collection();
  var log = Object.assign({
    'debug': function debug() {},
    'info': function info() {},
    'warn': console.warn,
    'error': console.error
  }, options && options.log);
  var api = {
    hook: hook,
    log: log,
    request: request.defaults(parseClientOptions(options, log, hook))
  };
  plugins.forEach(function (pluginFunction) {
    return pluginFunction(api, options);
  });
  return api;
}